import React from "react";
import { Route, Link, withRouter } from "react-router-dom";
import '../../style/registrationPage.scss';
import LoginContainer from "../Login/LoginContainer";

class Registration extends React.Component {
    constructor(props){
        super(props);
    }
render(){
    return(
        <div className="wrapper">
        <form className="registration_block" onSubmit={this.props.createNewUser}>
            <div className="sign_up">
                <h1>Sign up</h1>
                <p>who you are?</p>
            </div>
            <div className="icons_role">
                    <div className="user_icon">
                        <button className="user_icon_button" onClick={this.props.chooseRole}><i className="fa fa-user-plus"></i>User</button>
                </div>
                <div className="editor_icon">
                    <button className="editor_icon_button" onClick={this.props.chooseRole}><i className="fa fa-user-edit"></i>Editor</button>
                </div>
            </div>
            <div className="input_block" >
                <input required placeholder="Username" type="text" name="login" id="username" />
                <input required placeholder="Password" type="password" name="password" id="password" />
            </div>
            <div className="sign_block_btn">
                <button className="sign_btn">sign up</button>
            </div>
            <div className="login_block">
                <p>Already have an account. <Link to="/login" className="login_redirect">Login here</Link></p>
            </div>
            
        </form>
        </div>
    )
    }
}

export default withRouter(Registration);