import React from "react";
import Registration from "./Registration";
import Controller from "../../app/controller";
import SignUp from '../../app/registration';
import LoginContainer from '../Login/LoginContainer';
import {Route, Link} from "react-router-dom";
export default class RegistrationContainer extends React.Component{
    
        state = {
            login: '',
            password: '',
            role: ''
        }
    chooseRole(e){
        e.preventDefault(); 
        debugger
        if(e.target.innerText =='User'){
            document.querySelector('.user_icon_button').style.color = 'blue';
            document.querySelector('.editor_icon_button').style.color = 'gray';
            sessionStorage.setItem('role', e.target.innerText);
        } else if(e.target.innerText == 'Editor') {
            document.querySelector('.user_icon_button').style.color = 'gray';
            document.querySelector('.editor_icon_button').style.color = 'blue';
            sessionStorage.setItem('role', e.target.innerText);
        }
    }
    createNewUser(e) {
        e.preventDefault();
        let cardsController = new Controller('http://localhost:3030/api/');
        let signup = new SignUp(cardsController);
        let inputLogin = e.target.elements.login.value, inputPassword=e.target.elements.password.value;
        console.log(inputPassword, inputLogin);
        let url = `accounts/${inputLogin}`;
        cardsController.makeRequest("GET",url,'')
           .then(data => signup.checkLogin(data));
        
    }

    render(){
        return(
        <div>
        <Registration createNewUser={this.createNewUser} chooseRole={this.chooseRole} role={this.state.role} />
        
        </div>
        )
    }
}