
export default class SignUp {


    constructor(cardsController){
        this.form = document.querySelector('.registration_block');
        this.userButton = document.querySelector('.user_icon_button');
        this.editorButton = document.querySelector('.editor_icon_button');
        this.submitButton = document.querySelector('.sign_btn');
        this.loginRedirect =  document.querySelector('.login_redirect');
        this.login = document.getElementById('username');
        this.password = document.getElementById('password');
        this.cardsController = cardsController;
        this.totalRole= sessionStorage.getItem('role');
        console.log(this.form, this.userButton);
        console.log(this);  
    }

    submit(){
        let param = `login=${this.login.value}&password=${this.password.value}&role=${this.totalRole}`;
        let url = "create-account";
        console.log(this.totalRole);
        sessionStorage.setItem('user', this.login.value);
        sessionStorage.setItem('role', this.totalRole);
        this.cardsController.makeRequest("POST",url, param)
        .then(data => this.redirect());
        this.redirect();
    }
    
    createLogin(){
        let url = `accounts/${this.login.value}`;
        this.cardsController.makeRequest("GET",url,'')
        .then(data => this.checkLogin(data));
    }
    
    checkLogin(data) {
        if(!data || data == '') {
            this.submit();
        } else if(data != ''){
            alert('please, enter another login.');  
        }
    }
    redirect(){
        document.location.href = "../public/mainPage.html";
    }
    
    checkInputs(e) {
        console.log(this);
        if(this.login.value == '' || this.password.value == '' || this.totalRole == ''){
            alert('please full fill all inputs');
        } else this.createLogin();
    
    
    }

    addEvents(){
        if(this.form){
        this.userButton.addEventListener('click', e =>{
            console.log(this);
            this.totalRole = e.target.innerText;
            this.userButton.style.color = 'blue';
            this.editorButton.style.color = 'black';
        });
        this.editorButton.addEventListener('click', e =>{
            this.totalRole = e.target.innerText;
            this.userButton.style.color = "black";
            this.editorButton.style.color = 'blue';
        })
        this.submitButton.addEventListener('click', e => {this.checkInputs()});
        this.loginRedirect.addEventListener('click', function() {
            document.location.href = "../public/loginPage.html";
        })
        this.form.addEventListener('click', e => {
            e.preventDefault();
        })
    }
    }
    


}
