import React from "react";
import { withRouter } from "react-router-dom";
import '../../style/registrationPage.scss'
import LoginContainer from "./LoginContainer";
class Login extends React.Component{
    render() {
        return(
            <div className="wrapper">
                <form className="login_form">
                    <div className="sign_up">
                        <h1>Log In</h1>
                    </div>
                    <div className="input_block" >
                        <input placeholder="Username" type="text" name="login" id="usernameLogin" />
                        <input placeholder="Password" type="password" name="password" id="passwordLogin" />
                    </div>
                    <div className="sign_block_btn">
                        <button className="sign_btn_log">submit</button>
                    </div>
                    <div className="login_block">
                        <p>Already haven't an account. <a href='./registrationPage.html' className="login_redirect">Sign up here</a></p>
                    </div>
                </form>
            </div>
        )
    }
}

export default withRouter(Login)